function reducer(state, action) {
  if (state === undefined) {
    return { content: "about" };
  }

  switch (action.type) {
    case "about":
      return { content: "about" };
    case "knowledge":
      return { content: "knowledge" };
    case "portfolio":
      return { content: "portfolio" };
    case "blogs":
      return { content: "blogs" };
    case "contact":
      return { content: "contact" };
    default:
      return state;
  }
}

export default reducer;
