import React, { Component } from 'react';
import './Navi.css';

class Navi extends Component {
  render() {
    function style(item, show) {
      return {textDecoration: show === item ? "underline" : "none"}
    }
    
    var show = this.props.contentToShow;

    return (
      <nav>
        <ul class="site-nav">
          <li>
            <button onClick={this.props.showAbout}
               style={style("about", show)}>About</button>
          </li>
          <li>
            <button onClick={this.props.showKnowledge}
                  style={style("knowledge", show)}>Knowledge</button>
          </li>
          <li>
            <button onClick={this.props.showPortfolio}
                  style={style("portfolio", show)}>Portfolio</button>
          </li>
          {/* <li>
              <button onClick={this.props.showBlogs}
              style={style("blogs", show)}>Blogs</button>
              </li> */}
          <li>
            <button onClick={this.props.showContact}
                  style={style("contact", show)}>Contact</button>
          </li>
        </ul>
      </nav>
    );
  }
}

export default Navi;
